





































































































export default {
	data() {
		return {
			systemInfo: {}
		}
	},
	onUnload:function(){
		this.systemInfo = {};
	},
	methods: {
		getSystemInfo: function () {
			uni.getSystemInfo({
				success: (res) => {
					this.systemInfo = res
				}
			})
		}
	}
}



